import wechat from '../../../components/wechat.vue'
import interfaces from '../../../utils/interface.js'






export default {
	data() {
		return {
			goodsInfo: [],
			goodsInfoCur: [],
			money: '',
			wechatShow: false, //显示分享
			loadingText: "正在加载....",
			loading: true,
			goods: false,
			kanId: '',
			showGameOver: ''

		}
	},
	components: {
		wechat
	},
	onShareAppMessage: function(res) {
		console.log(this.goodsInfoCur)
		var a = {
			k: this.goodsInfoCur.id,
			g: this.goodsInfoCur.child_id
		}
		if (res.from === 'button') {
			// 来自页面内转发按钮
		}
		return {
			title: this.goodsInfoCur.title,
			path: '/pages/game/index?scanCode=' + JSON.stringify(a), // 路径，传递参数到指定页面。
			imageUrl: 'http://www.tiktokshopee.com' + this.goods.thumbimg,
		}
	},
	methods: {
		//立即分享
		share(item) {
			// this.wechatShow = true;
			this.goodsInfoCur = item;
			this.money = item.on_price;
			this.kanId = item.id;
			this.child = item.child_id;
			let a = {
				k: this.kanId,
				g: this.child,
				y: item.you,
			}
			uni.navigateTo({
				url: '/pages/game/index?scanCode=' + JSON.stringify(a), // 路径，传递参数到指定页面。
			})
		},
		toOrder(item) { //去下单
			console.log(item)
			item.game = '拍卖';
			item.num = 1;
			item.kan_id = item.id;
			uni.navigateTo({
				url: '../../order/base/createOrder?goodsInfo=' + JSON.stringify(item)
			})
		},
		delGame(e) { //删除商品
			this.request({
				url: interfaces.del_kan,
				method: "POST",
				data: {
					kan_id: e
				},
				success: ((res) => {
					if (res.error_code == 0) {
						uni.showToast({
							title: '删除成功',
							icon: 'none',
						})
						let self = this;
						setTimeout(function() {
							self.initData()
						}, 500)
					}
				})
			})

















		},
		async initData() {
			this.request({
				url: interfaces.get_kan, //获取用户开砍游戏记录
				method: "POST",
				success: ((res) => {
					console.log(res)
					if (res.error_code === 0) {
						res.data.forEach((item) => {
							item.end_time = this.timestampToTime(item.end_time)
						})
						this.goodsInfo = res.data;
						this.loading = false;
						if (this.goodsInfo.length < 1) {
							this.loading = true;
							this.loadingText = '暂无数据'
						}
					}
				})
			});















		},
		timestampToTime(cjsj) {
			let time = cjsj.toString()
			if (time.length == 10) {
				time = time * 1000
			}
			var date = new Date(time) //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear() + '-'
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
			var D = date.getDate() + ' '
			var h = date.getHours() + ':'
			var m = date.getMinutes() + ':'
			var s = date.getSeconds()
			return Y + M + D + h + m + s
		},
	},
	onLoad() {
		this.initData();
	}
}
